const welcomeHu = `
  <div class="column col-12 content-description">
    <p>A hatvanas évek vége felé nagyapámtól MABÉOSZ-tagságot és szép, új Festmény-bélyegeket kaptam karácsonyi ajándékba. Természetes folyamat volt ez, ekkoriban volt közel 240.000 tagja a MABÉOSZ-nak, a bélyeggyűjtés népszerűsége csúcsán volt. Az is természetes volt, hogy 1971-ben érdekes hétvégi program volt, mikor nagyapám kézen fogott és elvitt a Szépművészeti Múzeumba a bélyeg-világkiállításra.</p>
    <p>Nem gondoltam volna, hogy 51 évet kell majd várni egy újabb, Magyarországon rendezett Világkiállításra, de nagyon örülök, hogy részese lehettem a rendezvény megszületése körüli bábáskodásnak, valamint a megvalósításában is szerepet vállalhatok. Óriásit fordult a világ az elmúlt 50 évben, de a bélyeggyűjtés, mint talán legfontosabb hobbi, továbbra is milliók életének részese a világon. Az más kérdés, hogy pont kis hazánkban a szervezett bélyeggyűjtők száma drasztikusan lecsökkent, ám talán ez a rendezvény jelezheti e folyamat pozitív megváltozását. A Magyar Államtól és a Magyar Postától kapott jelentős támogatás részeként az eddigieknél jelentősebb marketingkampányt tudunk majd folytatni és ha a MABÉOSZ gondolkodásában is végre tudunk hajtani egy előremutató fordulatot, akkor talán ismét érdemes lesz szervezett bélyeggyűjtővé válni Magyarországon.</p>
    <p>A világkiállítás megrendezésének fő apropója a MABÉOSZ jogelődjének, a MABEOSZ-nak 100 évvel ezelőtt történt megalapítása. Magyarország sok nemzetközi és nemzeti szervezet megalakításában járt élen a 19. és a 20. században, az 1922-ben létrehozott MABEOSZ is az egyik első össz-nemzeti bélyeggyűjtő szervezet volt a világon. Így álltunk készen arra, hogy már egy évvel annak 1926-ban történt megalapítása után Magyarország rendes tagja lehessen a FIP-nek, a Nemzetközi Filatéliai Szövetségnek. A háború utáni rendszer 1952. februárjában három működő szervezet összevonásából új országos szervezetet hozott létre, ez óta létezik a MABÉOSZ, magának ennek a mostani szervezetnek tehát hetvenéves évfordulója lesz.</p>
    <p>Szeretnénk nagyon emlékezetes eseményt szervezni, ahol remélhetőleg már a vírusvilág ijesztő időszakán túl adhat egymásnak randevút a világ bélyeggyűjtő közössége. Mindenkit nagy szeretettel várunk, Budapest szépségéről és vendégszeretetéről talán már mindenki hallott, aki már volt itt, biztos visszavágyik, aki még nem, jöjjön, próbálja ki! Találkozunk 2022 tavaszán!
    <p style="margin: 0;">Dr. Homonnay Géza</p>
    <p style="margin: 0;">a MABÉOSZ elnöke</p>
  </div>
`;

const welcomeEn = `
  <div class="column col-12 content-description">
    <p>By the end of the sixties I have received the freshly issued Paintment-series from my grandfather for Christmas. This was a natural type of present at that time, the MABÉOSZ having 240.000 members and the popularity of philately at it's peak.  It was also natural that it was an interesting weekend program to visit the World Stamp Exhibition of Budapest in 1971 at the beautiful Museum of Fine Arts.</p>
    <p>I did not expect having to wait 51 years for the next Philatelic World Exhibition to be organized in Budapest, but now I am very happy to be part of the organization working on the new event. The world turned around in the last 50 years, but philately, as a most important hobby is part of the life of several million people even today. Though it is true that in Hungary the number of organized collectors diminished drastically, but maybe this event can be a milestone in the turning of this trend. Having recieved a significant support from the Hungarian State and the Hungarian Post we will be able to conduct a significant marketing campaign. And if we can turn around also the thinking of MABÉOSZ itself, maybe it will be worthwhile to be be an organized collector in Hungary yet again.</p>
    <p>One of the main reasons to organize this exhibition is the centenary of the predecessor of MABÉOSZ, the organization called MABEOSZ. Hungary was a pioneer in the foundation of many national and international organizations in the 19th and 20th century - the MABEOSZ, founded in 1922 was among the first national philetelical organizations in the world.  This made it possible that already one year after the foundation of the FIP, the International Philatelic Federation, Hungary became a member of it. After the second world war, in 1952, a new national federation came to life by merging together three philatelic organizations - this was MABÉOSZ, which will be 70 years old in 2022.</p>
    <p>We would like to organize a most memorable event, hopefully hosting the philatelic society of the world, very hopefully in a post-COVID era. We are inviting everybody - most of you have heard about the beauty of Budapest, those who were here surely want to come back, those who not should come and see it! See you in the spring of 2022!</p>
    <p style="margin: 0;">Géza Homonnay dr</p>
    <p style="margin: 0;">President of MABÉOSZ</p>
</div>
`;

