let selectedPage = 'home';
let selectedLang = 'en';

function changeLanguageToEn() {
  selectedLang = "en";
  var en = document.getElementById("en");
  en.classList.add('selected');
  var hu = document.getElementById("hu");
  hu.classList.remove('selected');

  var home = document.getElementById("home");
  var home1 = document.getElementById("home1");
  home.innerText = 'Home';
  home1.innerText = 'Home';
  var welcome = document.getElementById("welcome");
  var welcome1 = document.getElementById("welcome1");
  welcome.innerText = 'Welcome';
  welcome1.innerText = 'Welcome';
  var classes = document.getElementById("classes");
  var classes1 = document.getElementById("classes1");
  classes.innerText = 'Exhibition Classes';
  classes1.innerText = 'Exhibition Classes';

  var title = document.getElementById('title');
  var content = document.getElementById("content");
  if(selectedPage === 'home') {
    content.innerHTML = homeEn;
    title.innerText = "Home";
  } else if(selectedPage === 'welcome') {
    content.innerHTML = welcomeEn;
    title.innerText = "Welcome";
  } else if(selectedPage === 'classes') {
    content.innerHTML = classesEn;
    title.innerText = "Exhibition Classes";
  }
}

